body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.App {
  text-align: center;
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.header {
  background-color: #282c34;
  color: white;
  padding: 20px;
}

.header h1 {
  margin: 0;
}

.about-me, .skills, .projects, .contact {
  margin-top: 40px;
  padding: 20px;
}

.about-me h2, .skills h2, .projects h2, .contact h2 {
  color: #333;
  border-bottom: 2px solid #282c34;
  display: inline-block;
  margin-bottom: 20px;
}

.skills ul {
  list-style-type: none;
  padding: 0;
}

.skills li {
  background-color: #e0e0e0;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  display: inline-block;
}

.projects .project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.projects .project {
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  margin: 20px;
  padding: 20px;
  width: 300px;
}

.projects .project h3 {
  margin-top: 0;
}

.projects .project a {
  color: #282c34;
  text-decoration: none;
  font-weight: bold;
}

.contact a {
  color: #0073b1;
  text-decoration: none;
}

footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #282c34;
  color: white;
}
